* {
  box-sizing: border-box;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.wrapper {
  height: 100vh;
  background: linear-gradient(#141e30, #243b55);
  grid-template-rows: 1fr 1fr;
  display: grid;
}

a {
  text-decoration: none;
}

#mainnav {
  position: absolute;
  top: 0;
}

.container {
  width: 300px;
  height: 534px;
  background-color: #533557;
  margin: 35px auto 0;
  position: relative;
  overflow: hidden;
}

#logotyp {
  width: 200px;
}

#toggle {
  position: absolute;
  top: -100%;
  left: -100%;
}

header {
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab) 0 0 / 400% 400%;
  justify-content: center;
  align-items: center;
  animation: gradient 15s infinite;
  display: flex;
}

@keyframes gradient {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

.test {
  text-align: center;
}

footer {
  justify-content: center;
  align-items: center;
  display: flex;
}

#toggle:checked ~ .toggle-container .button-toggle:before {
  transform: translateY(-50%)rotate(45deg)scale(1);
}

#toggle:checked ~ .toggle-container .button-toggle:after {
  transform: translateY(-50%)rotate(-45deg)scale(1);
}

#toggle:checked ~ .nav {
  pointer-events: auto;
  margin-bottom: 100px;
  transform: translate(50px, 50px);
}

#toggle:checked ~ .nav .nav-item {
  color: #000;
  letter-spacing: 0;
  height: 40px;
  opacity: 1;
  margin-top: 0;
  line-height: 40px;
  transition: all .5s, opacity .1s;
  transform: scaleY(1);
}

#toggle:checked ~ .nav .nav-item:nth-child(1), #toggle:checked ~ .nav .nav-item:nth-child(1):before {
  transition-delay: .15s;
}

#toggle:checked ~ .nav .nav-item:nth-child(2), #toggle:checked ~ .nav .nav-item:nth-child(2):before {
  transition-delay: .1s;
}

#toggle:checked ~ .nav .nav-item:nth-child(3), #toggle:checked ~ .nav .nav-item:nth-child(3):before {
  transition-delay: 50ms;
}

#toggle:checked ~ .nav .nav-item:nth-child(4), #toggle:checked ~ .nav .nav-item:nth-child(4):before {
  transition-delay: 0s;
}

#toggle:checked ~ .nav .nav-item:before {
  opacity: 0;
}

.button-toggle {
  width: 100px;
  height: 150px;
  cursor: pointer;
  background-color: #0000;
  border: none;
  transition: all .6s;
  display: flex;
  position: absolute;
}

.button-toggle:before, .button-toggle:after {
  content: "";
  width: 50%;
  height: 5px;
  background-color: #000;
  transition: all .5s;
  position: absolute;
  top: 50%;
  left: 0;
}

.button-toggle:before {
  transform: translateY(-50%)rotate(45deg)scale(0);
}

.button-toggle:after {
  transform: translateY(-50%)rotate(-45deg)scale(0);
}

.nav {
  pointer-events: none;
  width: 40px;
  flex-direction: column;
  gap: 10px;
  margin: 25px 25px 20px;
  transition: all .5s;
  display: flex;
}

.nav-item {
  height: 20px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  transition: all .5s, opacity 1s;
  display: flex;
  transform: scaleY(.2);
}

.nav-item:nth-child(1), .nav-item:nth-child(1):before {
  transition-delay: 0s;
}

.nav-item:nth-child(2), .nav-item:nth-child(2):before {
  transition-delay: 50ms;
}

.nav-item:nth-child(3), .nav-item:nth-child(3):before {
  transition-delay: .1s;
}

.nav-item:nth-child(4), .nav-item:nth-child(4):before {
  transition-delay: .15s;
}

.nav-item:nth-child(1) {
  letter-spacing: -8px;
}

.nav-item:nth-child(2) {
  letter-spacing: -7px;
}

.nav-item:nth-child(n+4) {
  letter-spacing: -8px;
  opacity: 0;
  margin-top: -7px;
}

.nav-item:before {
  content: "";
  width: 100%;
  height: 2px;
  background-color: #000;
  transition: all .5s;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%)scaleY(5);
}

#logoutbtn, #profile {
  display: none;
}

.index-main {
  width: 100vw;
  height: auto;
  flex-direction: column;
  grid-row: 2 / 4;
  justify-content: center;
  align-items: center;
  display: flex;
}

.category-wrapper {
  width: 50vw;
}

.article {
  cursor: pointer;
}

.posts-h1 {
  color: #fff;
  margin-bottom: 7rem;
}

.article-category {
  color: #fff;
  width: 50vw;
  background: #00000080;
  border: none;
  border-radius: .5rem;
  margin: .5rem;
  padding: .5rem;
}

.article-category a {
  text-align: center;
  color: #fff;
}

